import HeaderUtilit from "../Utilit/HeaderUtilit";
import LoginUtilit from "../Utilit/LoginUtilit";

const ISACTIVE = 'ISACTIVE'
const CONSOLEERRORS = 'CONSOLEERRORS'
const CAPTCHA = 'CAPTCHA'

export const IsAutirezation = (id, login, email, isAuth) => {
    return {
        type: ISACTIVE,
        data: {id, login, email, isAuth}
    }
}
export const ConsoleErrors = (data) => {
    return {
        type: CONSOLEERRORS,
        data
    }
}
export const SetCaptcha = (url) => {
    return {
        type: CAPTCHA,
        url
    }
}


const initialStaste = {
    id: null,
    login: null,
    email: null,
    isAuth: false,
    errors: null,
    captcha: null
}

const AuthReducer = (state = initialStaste, action) => {

    switch (action.type) {
        case ISACTIVE :
            return {
                ...state,
                ...action.data,

            }
        case CONSOLEERRORS :
            return {
                ...state,
                errors: action.data

            }
        case CAPTCHA :
            return {
                ...state,
                captcha: action.url.url

            }
        default:
            return state
    }

}


export const AutorizationThunk = () => (dispatch) => {
    console.log(3)
    return HeaderUtilit.Autirizarion()
        .then(data => {
            if (data.resultCode === 0) {
                let {id, login, email} = data.data
                dispatch(IsAutirezation(id, login, email, true))
            }
        })

}
export const GetCaptchaThunk = () => (dispatch) => {
    LoginUtilit.GetCaptcha()
        .then(data => {
            console.log(data)
            dispatch(SetCaptcha(data))
        })

}

export const LoginPageThunk = (email, password, remeberMe,captcha=null) => {
    return (dispatch) => {
        LoginUtilit.LoginPage(email, password, remeberMe, captcha)
            .then(data => {
                if (data.resultCode === 0) {
                    dispatch(AutorizationThunk())
                } else {
                    if (data.resultCode === 10) {
                        dispatch(GetCaptchaThunk())
                    }
                    dispatch(ConsoleErrors(data.messages[0]))
                }
            })
    }
}

export const LogoutPageThunk = () => {
    return (dispatch) => {
        LoginUtilit.LogoutPage().then(data => {
            if (data.resultCode === 0) {
                dispatch(IsAutirezation(null, null, null, false))

            }
        })


    }
}
export default AuthReducer