import axios from 'axios';


const LoginUtilit = {

    LoginPage(email, password, remeberMe, captcha) {
        console.log(email, password, remeberMe, captcha)
        return axios.post('https://social-network.samuraijs.com/api/1.0/auth/login', {
            email, password, remeberMe, captcha
        }, {
            withCredentials: true,
            headers: {
                "API-KEY": '1fa31ee6-b610-4b4b-b27f-b82fea224749'
            }
        })
            .then(response => response.data)
    },
    LogoutPage() {
        return axios.delete('https://social-network.samuraijs.com/api/1.0/auth/login', {withCredentials: true})
            .then(response => response.data)
    },
    GetCaptcha() {
        return axios.get('https://social-network.samuraijs.com/api/1.0/security/get-captcha-url', {
            withCredentials: true,
            headers: {
                "API-KEY": '1fa31ee6-b610-4b4b-b27f-b82fea224749'
            }
        })
            .then(response => response.data)
    },
}


export default LoginUtilit

